.acb-size-type-field__common-tabs .components-tab-panel__tabs {
  margin-bottom: -34px;
}

.acb-typography-advanced {
  box-shadow: 0 3px 30px rgba(25, 30, 35, 0.1);
  border: 1px solid #e2e4e7;
  background: #fff;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  position: relative;
}

.acb-typography-advanced::before {
  border: 8px solid #e2e4e7;

  content: "";
  position: absolute;
  height: 0;
  width: 0;
  line-height: 0;

  top: -8px;
  right: 5%;

  border-bottom-style: solid;
  border-left-color: transparent;
  border-right-color: transparent;
  border-top: none;
  margin-left: -10px;
}

.acb-typography-advanced::after {
  border: 8px solid #fff;

  content: "";
  position: absolute;
  height: 0;
  width: 0;
  line-height: 0;

  top: -6px;
  right: 5%;

  border-bottom-style: solid;
  border-left-color: transparent;
  border-right-color: transparent;
  border-top: none;
  margin-left: -10px;
}

