.rfipbtn,
.rfipdropdown {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  font-size: 14px;
  line-height: 1.71429;
  vertical-align: baseline
}

.rfipbtn,
.rfipbtn *,
.rfipdropdown,
.rfipdropdown * {
  margin: 0;
  padding: 0;
  box-sizing: border-box
}

.rfipbtn input,
.rfipbtn select,
.rfipdropdown input,
.rfipdropdown select {
  font-size: 14px
}

.rfip {
  position: relative;
  margin: 8px 0;
  width: 100%;
  vertical-align: middle
}

.rfipbtn {
  width: 136px;
  display: flex;
  flex-flow: row nowrap;
  min-height: 50px;
  border-radius: 2px;
  cursor: pointer;
  transition: box-shadow .25s, border-color .25s;
  outline: 0 none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  height: 40px;
}

.rfipbtn--open {
  border-radius: 2px 2px 0 0
}

.rfipbtn__button {
  width: 48px;
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  height: auto;
  transition: background .25s, box-shadow .25s
}

.rfipbtn__button i {
  font-size: 26px;
  line-height: 26px;
  height: 26px;
  width: 26px;
  transition: transform .25s;
  margin-left: auto;
  margin-right: auto;
}

.rfipbtn__button--open i {
  transform: rotate(-180deg)
}

.rfipbtn__current {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
  flex: 0 0 86px;
  padding: 2px
}

.rfipbtn--multi {
  width: 258px
}

.rfipbtn--multi .rfipbtn__current {
  flex-flow: row wrap;
  justify-content: flex-start;
  flex-basis: 212px;
  align-content: center
}

.rfipbtn--multi .rfipbtn__current,
.rfipbtn__icon {
  -webkit-box-orient: horizontal
}

.rfipbtn__icon {
  margin: 2px;
  padding: 0;
  height: 28px;
  width: 48px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  border-radius: 2px
}

.rfipbtn__icon--empty {
  font-size: 14px;
  line-height: 16px;
  margin-left: 8px;
  text-align: center;
  text-transform: lowercase;
  font-style: italic
}

.rfipbtn__elm {
  display: flex;
  height: 28px;
  width: 28px;
  align-items: center;
  justify-content: center;
  font-size: 18px
}

.rfipbtn__elm img,
.rfipbtn__elm svg {
  height: 18px;
  width: 18px;
}

.rfipbtn__elm svg {
  fill : #333;
}

.rfipbtn__del {
  width: 18px;
  display: flex;
  height: 28px;
  align-items: center;
  justify-content: center;
  transition: background-color .25s;
  cursor: pointer
}

.rfipcategory {
  width: 100%;
  margin: 0 0 8px;
  position: relative
}

.rfipcategory select {
  width: 100%;
  display: block;
  height: 32px;
  line-height: 32px;
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  transition: border .25s, box-shadow .25s;
  background-color: transparent!important
}

.rfipcategory i {
  position: absolute;
  right: 2px;
  top: 0;
  font-size: 16px;
  line-height: 32px;
  z-index: -1
}

.rfipdropdown {
  width: 352px;
  position: absolute;
  left: 0;
  margin-top: -1px;
  z-index: 100000001;
  border-radius: 0 1px 4px 4px
}

.rfipdropdown__selector {
  overflow: hidden;
  padding: 16px
}

.rfipdropdown.fipappear-enter-active .rfipdropdown__selector,
.rfipdropdown.fipappear-exit-active .rfipdropdown__selector {
  transition: max-height .3s ease-out, padding .3s ease-out;
  padding: 16px
}

.rfipicons__pager {
  display: flex;
  flex-flow: row nowrap;
  height: 24px;
  line-height: 24px;
  align-items: center;
  margin-bottom: 8px
}

.rfipicons__num {
  width: 100px;
  margin-right: auto
}

.rfipicons__cp {
  width: 32px;
  height: 24px;
  line-height: 24px;
  text-align: right
}

.rfipicons__cp,
.rfipicons__sp,
.rfipicons__tp {
  margin-right: 8px
}

.rfipicons__arrow {
  margin-left: auto;
  width: 56px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: center;
  height: 24px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none
}

.rfipicons__right {
  margin-left: auto
}

.rfipicons__left,
.rfipicons__right {
  cursor: pointer;
  width: 24px;
  height: 24px;
  position: relative;
  transition: background-color .25s, border .25s;
  outline: 0 none;
  border-radius: 2px;
  font-size: 18px
}

.rfipicons__label {
  height: 22px;
  width: 22px;
  display: flex;
  align-items: center;
  justify-content: center
}

.rfipicons__label img {
  height: 18px;
  width: 18px
}

.rfipicons__selector {
  flex: 1 1 20%;
  flex-flow: row wrap;
  align-content: center;
  justify-content: flex-start
}

.rfipicons__ibox,
.rfipicons__selector {
  display: flex
}

.rfipicons__ibox {
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  transition: background-color .25s, border .25s;
  border-radius: 2px;
  outline: 0 none;
  font-size: 20px
}

.rfipicons__ibox img,
.rfipicons__ibox svg {
  max-height: 24px;
  height: 18px;
  width: 18px;
}

.rfipicons__ibox svg {
  fill: #333;
}

.rfipicons__ibox>* {
  transform: scale(1);
  transition: transform .25s;
  transform-origin: center
}

.rfipicons__ibox:hover>* {
  transform: scale(1.8)
}

.rfipicons__ibox--error {
  text-transform: lowercase;
  font-style: italic
}

.rfipicons__icon {
  width: 20%;
  height: 64px;
  padding: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer
}

.rfipicons__icon--error {
  display: block;
  padding: 16px;
  text-align: center;
  font-size: 24px;
  width: 100%;
  line-height: 1
}

.rfipsearch {
  width: 100%;
  margin: 0 0 8px
}

.rfipsearch input {
  width: 100%;
  display: block;
  height: 32px;
  line-height: 32px
